/* Hide scrollbar for Chrome, Safari and Opera */
::-webkit-scrollbar {
    display: none;
}
  
  /* Hide scrollbar for IE, Edge and Firefox */
html {
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
}


body {
    opacity: 1;
}
a {
    text-decoration: none;
}

p {
    min-width: 0;
}

.hdr-txt {
    font-family: 'Bungee'; 
}

.section {
    display: flex;
    justify-content: center;
    background: linear-gradient(180deg, #1E0040 0%, #370076 100%) 0% 0% no-repeat padding-box; ;
}

.section-label {
    width: 30vw;
    /* min-width: 20vw; */
    /* min-width: 400px; */
    /* max-width: 400px; */
    /* flex-grow: 1; */
    flex-shrink: 0;
    height: 100vh;
    overflow: hidden;
    background: linear-gradient(180deg, #1E0040 0%, #370076 100%) 0% 0% no-repeat padding-box;
}

.flip-gradient {
    background: linear-gradient(0deg, #1E0040 0%, #370076 100%) 0% 0% no-repeat padding-box;
}

.fix-right {
    position: fixed;
    top: 0;
    right: 0;
}

.fix-left {
    position: fixed;
    top: 0;
    left: 0;
}

.section-info {
    background-color: #31393C;
    /* min-width: 60vw; */
    /* width: 60vw; */
    flex-grow: 1;
    overflow: hidden;
}

.pin-bottom-right {
    position: absolute;
    bottom: 0;
    right: 0;
    top: auto;
}

.pin-bottom-left {
    position: absolute;
    bottom: 0;
    left: 0;
    top: auto;
}

.flex-right {
    margin-left: auto;
}

.flex-left {
    margin-right: auto;
}

.label {
    position: relative;
    text-align: center;
    top: 50%;
    transform: translate(0, -50%);
    font-size: 64px;
}

.hidden {
    display: none;
}
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
	color: white;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

