.overlay {
    position: absolute;
    width: 80%;
    height: 100%;
}

.overlay div {
    opacity: 0;
    width: 100%;
    transition: .5s ease;
    background-color: rgba(108, 138, 138, .7);
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 56px;
}

.overlay img {
    height: 100px;
}

.overlay a {
    height: 100px;
}

.overlay div:hover {
    opacity: 1;
}

